rice {\n        color: $primary-color;\n      }\n    }\n  }\n}\n\n.product__item__pic {\n  height: 360px;\n  position: relative;\n  overflow: hidden;\n\n  .label {\n    font-size: 12px;\n    color: $white-color;\n    font-weight: 500;\n    display: inline-block;\n    padding: 2px 8px;\n    text-transform: uppercase;\n    position: absolute;\n    left: 10px;\n    top: 10px;\n\n    &.new {\n      background: #36a300;\n    }\n\n    &.stockout {\n      background: $heading-color;\n    }\n\n    &.stockblue {\n      background: #0066bd !important;\n    }\n\n    &.sale {\n      background: $primary-color;\n    }\n  }\n}\n\n.product__hover {\n  position: absolute;\n  left: 0;\n  width: 100%;\n  bottom: 30px;\n  text-align: center;\n\n  li {\n    list-style: none;\n    display: inline-block;\n    margin-right: 10px;\n    position: relative;\n    top: 100px;\n    opacity: 0;\n\n    &:nth-child(1) {\n      @include transition(all 0.4s ease 0.1s);\n    }\n\n    &:nth-child(2) {\n      @include transition(all 0.4s ease 0.15s);\n    }\n\n    &:nth-child(3) {\n      @include transition(all 0.4s ease 0.2s);\n    }\n\n    &:last-child {\n      margin-right: 0;\n    }\n\n    &:hover {\n      a {\n        background: $primary-color;\n\n        span {\n          color: $white-color;\n          -webkit-transform: rotate(360deg);\n          transform: rotate(360deg);\n        }\n      }\n    }\n\n    a {\n      font-size: 18px;\n      color: $heading-color;\n      display: block;\n      height: 45px;\n      width: 45px;\n      background: $white-color;\n      line-height: 48px;\n      text-align: center;\n      border-radius: 50%;\n\n      @include transition(all, 0.5s);\n\n      span {\n        position: relative;\n        -webkit-transform: rotate(0);\n        transform: rotate(0);\n\n        @include transition(all, 0.3s);\n\n        display: inline-block;\n      }\n    }\n  }\n}\n\n.product__item__text {\n  text-align: center;\n  padding-top: 22px;\n\n  h6 {\n    a {\n      font-size