esome\", sans-serif;\n    }\n\n    i {\n      margin-right: 5px;\n    }\n  }\n\n  span {\n    font-size: 15px;\n    font-weight: 500;\n    color: #888;\n    display: inline-block;\n  }\n}\n","/* Product Details */\n\n.product-details {\n  padding-top: 70px;\n  padding-bottom: 50px;\n}\n\n.product__details__pic {\n  overflow: hidden;\n}\n\n.product__details__pic__left {\n  width: 22%;\n  max-height: 574px;\n  float: left;\n  overflow-y: auto;\n\n  .pt {\n    display: block;\n    margin-bottom: 20px;\n    cursor: pointer;\n    position: relative;\n\n    &::after {\n      content: \"\";\n      position: absolute;\n      width: 100%;\n      height: 100%;\n      left: 0;\n      top: 0;\n      background: #000;\n      opacity: 0;\n\n      @include transition(all 0.3s);\n    }\n\n    &.active::after {\n      opacity: 0.3;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n\n    img {\n      min-width: 100%;\n    }\n  }\n}\n\n.product__details__slider__content {\n  width: calc(78% - 20px);\n  float: left;\n  margin-left: 20px;\n}\n\n.product__details__pic__slider {\n  &.owl-carousel {\n    .owl-nav {\n      button {\n        position: absolute;\n        left: 10px;\n        top: 50%;\n        font-size: 22px;\n        color: $heading-color;\n        width: 40px;\n        height: 40px;\n        background: rgba(255, 255, 255, 0.7);\n        border-radius: 50%;\n        line-height: 44px;\n        text-align: center;\n        margin-top: -20px;\n\n        &.owl-next {\n          left: auto;\n          right: 10px;\n        }\n      }\n    }\n  }\n}\n\n.product__details__text {\n  h3 {\n    color: $heading-color;\n    font-weight: 600;\n    text-transform: uppercase;\n    margin-bottom: 12px;\n\n    span {\n      display: block;\n      font-size: 14px;\n      color: #444;\n      text-transform: none;\n      font-weight: 400;\n      margin-top: 5px;\n    }\n  }\n\n  .rating {\n    margin-bottom: 16px;\n\n    i {\n      font-size: 12px;\n      color: #e3c01c;\n      margin-right: -4px;\n    }\n\n    span {\n      font-size: 12px;\n      color: #666;\n      margin-left: 5px;\n    }\n  }\n\n  p {\n    color: #444;\n    margin-bottom: 28px;\n  }\n}\n\n.product__details__price {\n  font-size: 30px;\n  font-weight: 600;\n  color: $primary-color;\n  margin-bottom: 30px;\n\n  span {\n    font-size: 18px;\n    color: #b1b0b0;\n    text-decoration: line-through;\n    margin-left: 10px;\n    display: inline-block;\n  }\n}\n\n.quantity {\n  float: left;\n  margin-right: 10px;\n  margin-bottom: 10px;\n\n  & > span {\n    font-size: 14px;\n    color: $heading-color;\n    font-weight: 600;\n    float: left;\n    margin-top: 14px;\n    margin-right: 15px;\n  }\n}\n\n.pro-qty {\n  height: 50px;\n  width: 150px;\n  border: 1px solid #ebebeb;\n  border-radius: 50px;\n  padding: 0 20px;\n  overflow: hidden;\n  display: inline-block;\n\n  .qtybtn {\n    font-size: 14px;\n    color: #666;\n    cursor: pointer;\n    float: left;\n    width: 12px;\n    line-height: 46px;\n  }\n\n  input {\n    font-size: 14px;\n    color: #666;\n    font-weight: 500;\n    border: none;\n    float: left;\n    width: 84px;\n    text-align: center;\n    height: 48px;\n  }\n}\n\n.product__details__button {\n  overflow: hidden;\n  margin-bottom: 25px;\n\n  .cart-btn {\n    display: inline-block;\n    font-size: 14px;\n    color: $white-color;\n    background: $primary-color;\n    font-weight: 600;\n    text-transform: uppercase;\n    padding: 14px 30px 15px;\n    border-radius: 50px;\n    float: left;\n    margin-right: 10px;\n    margin-bottom: 10px;\n  }\n\n  ul {\n    float: left;\n\n    li {\n      list-style: none;\n      display: inline-block;\n      margin-right: 5px;\n\n      &:last-child {\n        margin-right: 0;\n      }\n\n      a {\n        display: inline-block;\n        height: 50px;\n        width: 50px;\n        border: 1px solid #ebebeb;\n        border-radius: 50%;\n        line-height: 50px;\n        text-align: center;\n        padding-top: 1px;\n\n  