ight: 20px;\n\n          &.active {\n            input {\n              & ~ .checkmark {\n                &::after {\n                  border-color: $white-color;\n                  opacity: 1;\n                }\n              }\n            }\n          }\n\n          &:last-child {\n            margin-right: 0;\n          }\n\n          input {\n            position: absolute;\n            visibility: hidden;\n\n            &:checked {\n              & ~ .checkmark {\n                &::after {\n                  border-color: $white-color;\n                  opacity: 1;\n                }\n              }\n            }\n          }\n\n          .checkmark {\n            position: absolute;\n            left: 0;\n            top: -10px;\n            height: 20px;\n            width: 20px;\n            background: #e31e2f;\n            border-radius: 50%;\n            content: \"\";\n\n            &.black-bg {\n              background: #111;\n            }\n\n            &.grey-bg {\n              background: #e4aa8b;\n            }\n\n            &::after {\n              position: absolute;\n              left: 3px;\n              top: 5px;\n              width: 13px;\n              height: 6px;\n              border: solid #fff;\n              border-width: 1.5px 1.5px 0 0;\n              -webkit-transform: rotate(127deg);\n              -ms-transform: rotate(127deg);\n              transform: rotate(127deg);\n              opacity: 0;\n              content: \"\";\n            }\n          }\n        }\n      }\n\n      .size__btn {\n        label {\n          font-size: 14px;\n          color: #666;\n          text-transform: uppercase;\n          cursor: pointer;\n          margin-right: 10px;\n          display: inline-block;\n          margin-bottom: 0;\n\n          &:last-child {\n            margin-right: 0;\n          }\n\n          &.active {\n            color: $primary-color;\n          }\n\n          input {\n            position: absolute;\n            visibility: hidden;\n          }\n        }\n      }\n\n      p {\n        margin-bottom: 0;\n        color: #666;\n      }\n    }\n  }\n}\n\n.product__details__tab {\n  padding-top: 80px;\n  margin-bottom: 65px;\n}\n\n.nav {\n  border-bottom: none;\n  justify-content: center;\n  position: relative;\n  margin-bottom: 40px;\n\n  &::before {\n    position: absolute;\n    left: 0;\n    top: 13px;\n    height: 1px;\n    width: 335px;\n    background: #e1e1e1;\n    content: \"\";\n  }\n\n  &::after {\n    position: absolute;\n    right: 0;\n    top: 13px;\n    height: 1px;\n    width: 335px;\n    background: #e1e1e1;\n    content: \"\";\n  }\n}\n\n.nav-item {\n  margin-right: 46px;\n\n  &:last-child {\n    margin-right: 0;\n  }\n\n  .nav-link {\n    font-size: 18px;\n    color: #666;\n    font-weight: 600;\n    border: none;\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n    padding: 0;\n\n    &.active {\n      color: $heading-color;\n    }\n  }\n}\n\n.tab-content {\n  .tab-pane {\n    h6 {\n      color: #666;\n      font-weight: 600;\n      margin-bottom: 24px;\n    }\n\n    p {\n      &:last-child {\n        margin-bottom: 0;\n      }\n    }\n  }\n}\n\n.related__title {\n  h5 {\n    font-size: 20px;\n    color: $heading-color;\n    font-weight: 600;\n    text-transform: uppercase;\n    margin-bottom: 35px;\n  }\n}\n","/* ---------------------\n  Blog Details\n----------------------- */\n.blog-details {\n  padding-top: 80px;\n  padding-bottom: 80px;\n}\n\n.blog__details__item {\n  background: $white-color;\n  margin-bottom: 24px;\n}\n\n.blog__details__item__title {\n  background: $white-color;\n  padding-top: 30px;\n  padding-right: 30px;\n  margin-top: -100px;\n  position: relative;\n  margin-right: 50px;\n\n  .tip {\n    font-size: 12px;\n    color: $white-color;\n    background: $primary-color;\n    text-transform: uppercase;\n    font-weight: 500;\n    display: inline-block;\n    padding: 2px 11px 1px;\n    border-radius: 5px;\n  }\n\n  h4